






















import { defineComponent, ref } from '@vue/composition-api';
import UploadDragger from '@/components/upload/index.vue';

export default defineComponent({
  components: {
    UploadDragger,
  },

  setup(_, { root }) {
    const isDragEnter = ref(false);
    async function handleUplodImage(fileObj: any) {
      // @ts-ignore
      root.searchImage = fileObj;
      root.$router.push({
        path: 'searchbyimage',
        query: {
          searchImage: fileObj,
        },
      });
    }

    return {
      isDragEnter,

      handleUplodImage,
    };
  },
});
